@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap);

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #2a2e30 !important;
  background: url(/link_in_bio/static/media/Logo.25d494a7.png) no-repeat;
  background-position: center;
  background-size: contain;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Animation-Img {

  height: 200px;
  width: 200px;
  background-color: var(--secondary);
  border-radius: 50%;
  z-index: 0;
  position: absolute;

}

.x {
  -webkit-animation: x 10s linear infinite alternate;
          animation: x 10s linear infinite alternate;
}
.y {
  -webkit-animation: y 7s linear infinite alternate;
          animation: y 7s linear infinite alternate;
}

@-webkit-keyframes x {
  100% {
    transform: translateX(calc(100vw - 200px));
  }
}

@keyframes x {
  100% {
    transform: translateX(calc(100vw - 200px));
  }
}

@-webkit-keyframes y {
  100% {
    transform: translateY(calc(100vh - 200px));
  }
}

@keyframes y {
  100% {
    transform: translateY(calc(100vh - 200px));
  }
}

:root {

  --backdropColor: #2a2e30;
  --secondary: #acc1cb;
  --nameColor: #f46530;

}

.App {
  text-align: center;
  
  min-height: 100vh;
  min-width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #f46530;
  color: var(--nameColor);

  font-family: 'Source Code Pro', monospace;

  background-color: rgba(255, 255, 255, .15);  
  -webkit-backdrop-filter: blur(2px);  
          backdrop-filter: blur(2px);

}

.Link {

  max-height: 20vh;
  background-color: white;
  padding: 2%;
  border-radius: 20px;
  opacity: 0.75;

  color: #f46530;

  color: var(--nameColor);
  z-index: 1;
}

.Link:hover,:active, :visited{
  opacity:1;
}

.Link:link {

  text-decoration: none;
}




.Image {

  max-height: 15vh;
  max-width: 15vh;
  padding: 0;

}

h3,h2 {

  margin: 0;
  z-index: 1;
}
h2 {
  padding: 5%;
}
